@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#timer{display:flex;align-items:center;font-weight:bold}#timer.forfeit{color:red}#timer.forfeit .button{cursor:default}#timer.won{color:green}#timer.won .button{cursor:default}svg{transition:.5s}svg.button{cursor:pointer}svg.button:hover{color:red}

#outer_display{margin:auto;max-width:800px}#outer_display #winner{font-size:30px;margin:10px}#outer_display #display{max-width:75vw;height:90vh;margin:auto;display:flex;flex-direction:column;justify-content:center;margin:20px}#outer_display #display #info{display:flex;justify-content:center;margin:5px;min-height:20px}#outer_display #display #info *{margin:0px 2vw}#outer_display #display #song_title{background:#666;margin:5px 0px;text-transform:uppercase;font-weight:bold}#outer_display #display #song_title.forfeit{background:#fd3636}#outer_display #display #song_title.won{background:#06f006}#outer_display #display #lyrics{position:relative;overflow-y:auto;padding-right:1vw;display:flex;justify-content:left;flex-wrap:wrap}#outer_display #display #lyrics #pause_overlay{position:absolute;padding-top:30px;top:0;right:0;left:0;bottom:0;background:white}#outer_display #display #lyrics .word{margin:3px 2px;padding:0px 3px;font-size:2vh;background-color:#cacaca;border-radius:3px;-webkit-touch-callout:none;-webkit-user-select:none;user-select:none;transition:.02s}#outer_display #display #lyrics .word.forfeit{color:red}#outer_display #display #lyrics .word.undiscovered{color:transparent}

::-webkit-scrollbar{z-index:1 !important;width:10px;-webkit-transition:.5s ease-in-out;transition:.5s ease-in-out;scroll-behavior:smooth !important;border-radius:15px !important}::-webkit-scrollbar-track{border-radius:15px !important;background:#e7e7e7;border-left:2px solid transparent}::-webkit-scrollbar-thumb{-webkit-transition:background-color .5s;transition:background-color .5s;background-color:black;cursor:pointer;border-radius:15px !important}::-webkit-scrollbar-thumb:hover{cursor:pointer}input{height:28px !important;padding:3px;outline:none;border-radius:5px;border:2px solid black;box-shadow:0px 0px red;height:20px;transition:.2s}input:focus{border:1.5px solid black;box-shadow:2px 2px red}input:disabled{background:#d9d9d9}button{-webkit-touch-callout:none;-webkit-user-select:none;user-select:none;border-radius:5px;background-color:black;color:white;border:2px solid black;cursor:pointer;transition:.2s;outline:none;text-transform:uppercase;font-weight:bold}button:hover{background-color:red;border:2px solid red}button:disabled{background-color:#333;border:2px solid #333;cursor:default}#game{height:100vh;max-width:100%;display:flex;overflow:hidden}#game #apptitle{font-family:'Pacifico', cursive !important;font-size:28px;cursor:pointer;-webkit-touch-callout:none;-webkit-user-select:none;user-select:none}

#sidebar{margin:0px;background-color:black;color:white;font-size:24px;text-align:right;width:400px;max-width:100vw;height:100%;transition:.5s}#sidebar *{transition:.5s}#sidebar #header{background-color:#4d4d4d;transition:.5s;display:flex;flex-wrap:nowrap}#sidebar #header .spacer{flex-grow:1}#sidebar #header #title{margin-left:12px;margin-top:0px}#sidebar #header svg{flex-shrink:0;margin-right:8px;align-self:center;cursor:pointer;vertical-align:text-bottom}#sidebar #header svg.icon-hidden{-webkit-transform:translate(-86px);transform:translate(-86px)}#sidebar #children{display:flex;flex-direction:column;width:400px;max-width:100vw}#sidebar.hidden{width:44px}#sidebar .hidden{-webkit-transform:translate(-400px);transform:translate(-400px)}#sidebar input,#sidebar button{min-height:30px !important;padding:3px;margin:10px 3px}

#search,#playlist{max-height:40%;text-align:center}#search svg,#playlist svg{margin-left:8px;flex-shrink:0;transition:.1s}#search svg.button,#playlist svg.button{cursor:pointer}#search svg.button:hover,#playlist svg.button:hover{color:red}#search svg.button:disabled,#playlist svg.button:disabled{color:blue}#search #heading,#playlist #heading{margin:7px 20px;margin-top:30px;display:flex;justify-content:space-between}#search #heading #playlist_title,#playlist #heading #playlist_title{margin:0px 8px;width:100%;border-radius:0px;background:transparent;outline:none;box-shadow:none;transition:.2s;color:white;font-size:25px;outline-offset:0px !important}#search #heading #playlist_title:empty,#playlist #heading #playlist_title:empty{display:block}#search #heading #playlist_title:focus,#playlist #heading #playlist_title:focus{border-bottom:1px solid red;outline-offset:0px !important}#search #heading #toolbar,#playlist #heading #toolbar{display:flex;align-items:center;border-radius:5px;font-size:18px;padding:2px 10px;padding-right:18px;background:white;color:black}#search #heading #toolbar #fileinput,#playlist #heading #toolbar #fileinput{display:none}#search #alt,#playlist #alt{font-size:15px;margin:10px}#search #search_bar,#playlist #search_bar{margin:10px 20px;display:flex;flex-wrap:nowrap;justify-content:center}#search #search_bar input,#search #search_bar button,#playlist #search_bar input,#playlist #search_bar button{flex-grow:1;font-size:15px;min-height:20px !important;padding:2px;margin:5px 0px;border:0px}#search #search_bar input,#playlist #search_bar input{padding:2px 8px}#search #search_bar button,#playlist #search_bar button{margin-left:10px}#search #search_results,#playlist #search_results{background-color:white;color:black;margin:5px 20px;padding:5px;border-radius:5px;max-height:30vh;overflow-y:scroll;overflow-x:hidden;display:flex;flex-direction:column;transition:.5s}#search #search_results.empty,#playlist #search_results.empty{max-height:0px !important;padding:0px}#search #search_results::-webkit-scrollbar-thumb,#playlist #search_results::-webkit-scrollbar-thumb{background-color:red}#search #search_results .result,#playlist #search_results .result{flex-shrink:0;padding:3px;border-radius:5px;display:flex;justify-content:space-between;align-items:center;margin:2px;background:#e6e6e6;font-size:15px;text-align:left;flex-wrap:nowrap}#search #search_results .result .info,#playlist #search_results .result .info{display:flex}#search #search_results .result .info img,#playlist #search_results .result .info img{align-self:center;height:40px !important;width:40px !important;margin-left:15px}#search #search_results .result .info .text,#playlist #search_results .result .info .text{display:flex;flex-direction:column;margin:0px 8px}#search #search_results .result *.song,#playlist #search_results .result *.song{color:red}#search #search_results .result *.artist,#playlist #search_results .result *.artist{color:black;text-decoration:underline;cursor:pointer}#search #search_results .result svg,#playlist #search_results .result svg{margin-right:30px;flex-shrink:0;font-size:25px;transition:.1s}#search #search_results .result svg.button,#playlist #search_results .result svg.button{cursor:pointer}#search #search_results .result svg.button:hover,#playlist #search_results .result svg.button:hover{color:red}#search #playlist_length,#playlist #playlist_length{font-size:14px}

#modal{z-index:1;position:absolute;height:100vh;width:100vw;overflow:hidden;background:rgba(0,0,0,0.5);display:flex;justify-content:center;align-items:center}#modal #body{background:white;border-radius:10px;width:50%;max-height:70%;padding:10px 0px}#modal #body #title{font-size:20px;font-weight:bold}#modal #body #songs{background-color:white;color:black;margin:5px 20px;padding:5px;border-radius:5px;height:40vh;overflow-y:scroll;overflow-x:hidden;display:flex;flex-direction:column;transition:.5s}#modal #body #songs #loading{margin:auto}#modal #body #songs.empty{max-height:0px !important;padding:0px}#modal #body #songs::-webkit-scrollbar-thumb{background-color:red}#modal #body #songs .result{padding:3px;border-radius:5px;display:flex;justify-content:space-between;align-items:center;margin:2px;background:#e6e6e6;font-size:15px;text-align:left;flex-wrap:nowrap}#modal #body #songs .result .info{display:flex}#modal #body #songs .result .info img{height:40px !important;width:40px !important;margin-left:15px}#modal #body #songs .result .info .text{display:flex;flex-direction:column;margin:0px 8px}#modal #body #songs .result a.song{color:red}#modal #body #songs .result a.artist{color:black}#modal #body #songs .result svg{margin-right:30px;flex-shrink:0;font-size:25px;transition:.1s}#modal #body #songs .result svg.button{cursor:pointer}#modal #body #songs .result svg.button:hover{color:red}#modal #body #pagination{display:flex;justify-content:space-evenly;font-weight:bold}#modal #body #pagination .link{text-decoration:underline;cursor:pointer}#modal #body #pagination .link.disabled{color:grey;cursor:default}

#home{overflow:hidden;margin:auto;max-width:700px;margin:2vh auto}#home #head{max-width:90%;margin:auto;text-align:left;padding-bottom:1vh;border-bottom:1px solid black;margin-bottom:1vh}#home #head .big{-webkit-touch-callout:none;-webkit-user-select:none;user-select:none;font-family:'Pacifico', cursive !important;font-size:45px;text-align:center !important}#home #nav{max-width:90%;margin:auto;padding-bottom:1vh;border-bottom:1px solid black;margin-bottom:1vh}#home #nav svg{color:black;cursor:pointer;font-size:25px;padding:4px 0px;margin:0px 10px;transition:.2s}#home #nav svg:hover{color:red}#home #nav svg.active{border-bottom:1px solid black}#home .home_body{max-width:90%;margin:auto}#home .home_body .title{-webkit-touch-callout:none;-webkit-user-select:none;user-select:none;font-family:'Pacifico', cursive !important;font-size:150%;font-weight:bold;margin-bottom:10px}#home .home_body .faq,#home .home_body .about,#home .home_body .settings{max-height:50vh;overflow:auto}#home .home_body .settings .MuiSwitch-switchBase.Mui-checked{color:red !important}#home .home_body .settings .MuiTypography-body1{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif !important}#home .home_body ul{text-align:left}#home .home_body ul p{padding:5px 20px}

.App {
  height: 100vh;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

